import Antimicrobials from "@/xdata/antimicrobials";
import {
    validateResponse,
    handleError,
    addAuthorizationHeader,
    setJsonContentType
  } from "@/mixins/fetch";

const PAGE_SIZE = 20;

export const antimicrobial = {
  state: {
        antimicrobials: {},
        antimicrobialsPageNumber: 1,
  },
  mutations: {
    updateAntimicrobials(state, antimicrobials) {
        state.antimicrobials = antimicrobials
    },
    setAntimicrobialsPageNumber(state, pageNumber) {
        state.antimicrobialsPageNumber = pageNumber
    }
  },
  getters: {
    antimicrobials(state) {
        return state.antimicrobials.slice((PAGE_SIZE*state.antimicrobialsPageNumber)-PAGE_SIZE, PAGE_SIZE*state.antimicrobialsPageNumber)
    }
  },
  actions: {
    async getAntimicrobials({ commit }, pageToken) {
        commit("updateAntimicrobials", Antimicrobials);
        try {
            const response = await fetch(
                `${process.env.VUE_APP_ANTIMICROBIALSAPI_URL}/antimicrobials?page_token=${pageToken}`
            );
            const validRes = await validateResponse(response);
            const antimicrobials = await validRes.json();
            commit("updateAntimicrobials", antimicrobials);
            } catch (err) {
            handleError(err);
        }
    },
  }
};
