import {types} from './types';

export default {
	[types.SET_SETTINGS]: (state, settings) => {
		state.settings = settings
	},
	[types.SET_JOBS]: (state, jobs) => {
		state.jobs = jobs
	},
	[types.SET_STARED_HOSPITALS]: (state, staredHospitals) => {
		state.staredHospitals = staredHospitals
	},
	[types.ADD_JOB]: (state, job) => {
		state.jobs.push(job)
	},
	[types.REMOVE_JOB]: (state, jobId) => {
		state.jobs = state.jobs.filter(job => job.jobId !== jobId)
	},
	[types.ADD_STARED_HOSPITAL]: (state, hospital) => {
		state.staredHospitals.push(hospital)
	},
	[types.REMOVE_STARED_HOSPITAL]: (state, hospitalId) => {
		state.staredHospitals.filter(hospital => hospital.hospitalId !== hospitalId)
	}
};
