export const validateResponse = async function(response) {
  if (!response.ok) {
    const err = await response.json();
    throw err;
  }
  return response;
};

export const handleError = function(err) {
  fetch("x.com", {
    body: err,
    method: "POST"
  });
};

export const addAuthorizationHeader = function(headers) {
  headers.append("Authorization", `Bearer ${localStorage.getItem("token")}`);
  return headers;
};

export const addDefaultAuthorizationHeader = function(headers) {
  headers.append("Authorization", `Bearer ${localStorage.getItem("default")}`);
  return headers;
};

export const setJsonContentType = function(headers) {
  headers.append("Content-Type", "application/json");
  return headers;
};
