import Pathogens from "@/xdata/pathogens";
import {
    validateResponse,
    handleError,
    addAuthorizationHeader,
    setJsonContentType
  } from "@/mixins/fetch";

const PAGE_SIZE = 20;

export const pathogen = {
  state: {
        pathogens: [],
        pathogensPageNumber: 1,
  },
  mutations: {
    updatePathogens(state, pathogens) {
        state.pathogens = pathogens
    },
    setPathogensPageNumber(state, pageNumber) {
        state.pathogensPageNumber = pageNumber
    }
  },
  getters: {
    pathogens(state) {
        return state.pathogens.slice((PAGE_SIZE*state.pathogensPageNumber)-PAGE_SIZE, PAGE_SIZE*state.pathogensPageNumber)
    },
    allPathogens(state) {
      return state.pathogens
    }
  },
  actions: {
    async getPathogens({ commit }, pageToken) {
        commit("updatePathogens", Pathogens);
        try {
            const response = await fetch(
                `${process.env.VUE_APP_PATHOGENSAPI_URL}/pathogens?page_token=${pageToken}`
            );
            const validRes = await validateResponse(response);
            const pathogens = await validRes.json();
            commit("updatePathogens", pathogens);
            } catch (err) {
            handleError(err);
        }
    },
  }
};
