import { addDefaultAuthorizationHeader, setJsonContentType, validateResponse } from '@/mixins/fetch';

export const registerModule = {
	state: {
		register: {
			errors: [],
            dialog: false,
            success: false,
		},
	},
	mutations: {
		['SET_REGISTER_ERRORS'] (state, errors) {
			state.register.errors = errors
        },
        ['SHOW_SUCCESS_DIALOG'] (state) {
            state.register.success = true
        },
		['SHOW_REGISTER_DIALOG'] (state) {
			state.register.dialog = true
		},
		['CLOSE_SUCESS_DIALOG'] (state) {
			state.register.success = false
        },
		['CLOSE_REGISTER_DIALOG'] (state) {
			state.register.dialog = false
		},
	},
	getters: {
		registerDialog: (state) => state.register.dialog,
		successDialog: (state) => state.register.success,
		registerErrors: (state) => state.register.errors,
	},
	actions: {
		async register({ commit, dispatch, rootGetters }, user) {
            commit('APP_LOADING', true, {rootState: true})
			if (user.password !== user.confirmPassword) {
				commit('SET_REGISTER_ERRORS', ['Password must match'])
				return
			}
			
			// Perform the commit on state regardless whether fetch it succeeds or fail
			const commits = () => {
				commit('APP_LOADING', false, {rootState: true})
				commit('CLOSE_REGISTER_DIALOG')
				commit('SHOW_SUCCESS_DIALOG')
			}

			try {
                const url = `${process.env.VUE_APP_BASE_URL}/api/account/users`
                const headers = setJsonContentType(addDefaultAuthorizationHeader(new Headers()))
                const registerRequest = {
					user: {
						email: user.email,
						phone: user.phone,
						firstName: user.firstName,
						lastName: user.lastName,
						profileUrlThumb: 'https://antibug.co.ke/images/default/profile/thumb',
						profileUrlMedium: 'https://antibug.co.ke/images/default/profile/medium',
						profileUrlLarge: 'https://antibug.co.ke/images/default/profile/large',
					},
					privateProfile: {
						password: user.password,
						confirmPassword: user.confirmPassword,
					}
				}
				const body = JSON.stringify(registerRequest)
                const response = await fetch(url, {
                    method: 'POST',
                    cache: 'no-cache',
                    body,
                    headers,
                });
                await validateResponse(response)
				commits()
				dispatch('loginWithEmail', { email: user.email, password: user.password})
            } catch (err) {
				commit('SET_REGISTER_ERRORS', [err.message])
				commit('APP_LOADING', false, {rootState: true})
            }
		},
		async registerWithEmail({ commit }, loginPayload) {
			// Do stuff
			console.log(loginPayload)
		}
	}
};
