<template>
    <v-layout column card pa-3 mb-3>
          <v-flex title font-weight-light mb-2>Antibiogram Filter Summary</v-flex>
          <v-layout>
            <v-flex xs6 font-weight-light subheading>Scope:</v-flex>
            <v-flex xs6 font-weight-medium>{{antibiogramFilter.scope}}</v-flex>
          </v-layout>
          <v-divider></v-divider>
          <v-layout>
            <v-flex xs6 font-weight-light subheading>Duration:</v-flex>
            <v-flex xs6 font-weight-medium>{{antibiogramFilter.duration}}</v-flex>
          </v-layout>
          <v-layout v-if="antibiogramFilter.advanced" column>
            <v-divider></v-divider>
            <v-layout>
              <v-flex xs6 font-weight-light subheading>Isolates Gender:</v-flex>
              <v-flex xs6 font-weight-medium>{{antibiogramFilter.advance.gender}}</v-flex>
            </v-layout>
            <v-divider></v-divider>
            <v-layout>
              <v-flex xs6 font-weight-light subheading >Isolates Age:</v-flex>
              <v-flex xs6 font-weight-medium>
                <span v-if="!antibiogramFilter.advance.useWeeksForMinAge">
                  {{antibiogramFilter.advance.ageMin}} Years
                </span>
                <span v-else>
                  {{antibiogramFilter.advance.weekMin}} Weeks
                </span> - 
                <span v-if="!antibiogramFilter.advance.useWeeksForMaxAge">
                  {{antibiogramFilter.advance.ageMax}} Years
                </span>
                <span v-else>
                  {{antibiogramFilter.advance.weekMax}} Weeks
                </span>
              </v-flex>
            </v-layout>
          </v-layout>            
          <v-divider></v-divider>
          <v-layout v-if="countyScope" wrap>
            <v-flex font-weight-light subheading>Counties:</v-flex>
            <v-layout row wrap>
              <v-flex v-for="(county, i) in antibiogramFilter.values" :key="i">
                <v-chip color="orange" text-color="white">
                  <v-icon left>place</v-icon>
                  {{county.name}}
                </v-chip>
              </v-flex>
            </v-layout>
          </v-layout>
          <v-layout v-if="subCountyScope" wrap>
            <v-flex font-weight-light subheading>Sub-Counties:</v-flex><v-flex>{{antibiogramFilter.values}}</v-flex>
            <v-layout row wrap>
              <v-flex v-for="(region, i) in antibiogramFilter.values" :key="i">
                <v-chip color="orange" text-color="white">
                  <v-icon left>place</v-icon>
                  {{region.name}}
                </v-chip>
              </v-flex>
            </v-layout>
          </v-layout>
          <v-layout v-if="hospitalScope" wrap justify-start>
            <v-flex font-weight-light subheading>Facilities:</v-flex>
            <v-layout row wrap>
              <v-flex v-for="(facility, i) in antibiogramFilter.values" :key="i">
                <v-chip color="orange" text-color="white">
                  <v-icon left>local_hospital</v-icon>
                  {{facility.name}}
                </v-chip>
              </v-flex>
            </v-layout>
          </v-layout>
          <v-dialog
            v-model="viewAntibiogramFilter"
            :fullscreen="$vuetify.breakpoint.xsOnly"
            max-width="500"
            persistent
          >
            <template v-slot:activator="{ on }">
              <v-layout>
              <v-btn
                block
                color="primary"
                flat
                v-on="on"
                @click="viewAntibiogramFilter = !viewAntibiogramFilter"
              >
                Change Antibiogram Filter
                <v-icon right>edit</v-icon>
              </v-btn>                
              </v-layout>
            </template>
            <antibiogram-filter @close="viewAntibiogramFilter = false" :routeUrl="routeUrl"></antibiogram-filter>
          </v-dialog>
          <antibiogram-guideline></antibiogram-guideline>
        </v-layout>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  props: {
    routeUrl: {
      type: String,
      default: "/antibiogram"
    }
  },
  computed: {
    ...mapGetters(['antibiogramFilter']),
    countyScope() {
      return this.antibiogramFilter.scope === 'County Level'
    },
    subCountyScope() {
      return this.antibiogramFilter.scope === 'Sub-County Level'
    },
    hospitalScope() {
      return this.antibiogramFilter.scope === 'Facility/Hospital'
    }
  },
  data() {
    return {
      scope: '',
      viewAntibiogramFilter: false,
    }
  }
};
</script>

