import Antibiogram from "@/xdata/antibiogram";

export const antibiogram ={
  state: {
    antibiograms: Antibiogram,
  },
  mutations: {
    updateAntibiogramFilter(state, filter) {
      console.log(filter)
      state.antibiograms.filter = filter
    }
  },
  getters: {
    antibiogramFilter(state) {
      return state.antibiograms.filter
    },
    gramPositive(state) {
      return state.antibiograms.antibiograms.filter(susceptibility => susceptibility.category === "Gram (+)")
    },
    gramNegative(state) {
      return state.antibiograms.antibiograms.filter(susceptibility => susceptibility.category === "Gram (-)")
    },
    others(state) {
      return state.antibiograms.antibiograms.filter(susceptibility => susceptibility.category === "Other")
    },
    antibiograms(state) {
      return state.antibiograms.antibiograms
    }
  },
  actions: {}
};
