<template>
  <v-card>
    <v-toolbar v-if="$vuetify.breakpoint.xsOnly" dark color="primary">
      <v-btn icon dark @click="CLOSE_LOGIN_DIALOG">
        <v-icon>close</v-icon>
      </v-btn>
      <v-toolbar-title class="title white--text"
        >AntiBug
        <span><v-icon right>healing</v-icon></span>
      </v-toolbar-title>
      <v-spacer></v-spacer>
    </v-toolbar>
    <!-- <v-toolbar-side-icon @click.stop="drawer = !drawer" class="hidden-sm-and-up"></v-toolbar-side-icon> -->
    <v-card-title>
      <v-layout column justify-center align-center>
        <v-flex
          primary--text
          subheading
          font-weight-bold
          mb-1
          v-if="!$vuetify.breakpoint.xsOnly"
          >Antibug
          <span><v-icon right color="primary" small>healing</v-icon></span>
        </v-flex>
        <v-divider></v-divider>
        <v-flex title font-weight-light mt-1
          >Login For A Customize Experience</v-flex
        >
      </v-layout>
    </v-card-title>
    <v-card-text>
      <v-flex v-if="loginErrors.length >= 1">
        <v-layout red--text border-error pa-1 mb-2>
          <v-layout column v-for="(loginError, i) in loginErrors" :key="i">
            <v-flex text-xs-center xs12>
              {{ loginError }}
            </v-flex>
          </v-layout>
        </v-layout>
      </v-flex>
      <v-layout column>
        <v-form ref="form" v-model="valid" lazy-validation>
          <v-flex>
            <v-text-field
              label="Email Address or Phone"
              required
              v-model="login.email"
              :rules="rules.emailRules"
              validate-on-blur
              prepend-icon="email"
            ></v-text-field>
          </v-flex>
          <v-flex>
            <v-text-field
              label="Password"
              required
              v-model="login.password"
              :rules="rules.passwordRules"
              :type="show ? 'text' : 'password'"
              :prepend-icon="show ? 'visibility' : 'visibility_off'"
              @click:prepend="show = !show"
              validate-on-blur
            ></v-text-field>
          </v-flex>
          <v-flex pb-1>
            <v-btn v-if="loading" :loading="loading" block color="primary">Signing In...</v-btn>
            <v-btn v-else @click="loginAccount" block color="primary">Login</v-btn>
          </v-flex>
        </v-form>
        <v-divider></v-divider>
        <v-layout column pt-1 justify-center align-center>
          <v-flex>Or</v-flex>
          <v-btn @click="loginGoogle" block flat
            >Login With Google
            <v-avatar size="30">
              <v-img
                src="https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQhAR89CKjGGbr2BbyTNwFo4adLHY5DTG6jtMgK1FWsVv2oMSpe"
                height="24"
                width="24"
              ></v-img>
            </v-avatar>
          </v-btn>
        </v-layout>
      </v-layout>
    </v-card-text>
    <v-card-actions>
      <v-flex
        >No Account?
        <span class="primary--text utoh pcoh" @click="register"
          >Register</span
        ></v-flex
      >
      <v-spacer></v-spacer>
      <v-btn flat color="primary" @click="CLOSE_LOGIN_DIALOG">Cancel</v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
import { mapMutations, mapActions, mapGetters } from "vuex";

export default {
  computed: {
    ...mapGetters(['loginErrors', 'loading']),
  },
  data() {
    return {
      show: false,
      valid: false,
      login: {
        email: "",
        password: ""
      },
      rules: {
        emailRules: [v => !!v || "Email Address or Phone is required"],
        passwordRules: [v => !!v || "Password is required"]
      }
    };
  },
  methods: {
    ...mapActions(["loginWithEmailOrPhone"]),
    ...mapMutations(["CLOSE_LOGIN_DIALOG", "SHOW_REGISTER_DIALOG"]),
    loginGoogle() {},
    loginAccount() {
      if (this.$refs.form.validate()) {
        this.loginWithEmailOrPhone(this.login);
      }
    },
    register() {
      this.CLOSE_LOGIN_DIALOG();
      this.SHOW_REGISTER_DIALOG();
    }
  }
};
</script>

<style></style>
