import Facilities from "@/xdata/facilities";import {
  validateResponse,
  setJsonContentType,
  addAuthorizationHeader,
} from "@/mixins/fetch";

export const culture = {
  state: {
    culture: {
      labTechId: '',
      labTechName: '',
      hospitalId: '',
      countyCode: '',
      subCountyCode: '',
      patientId: '',
      patientName: '',
      patientAge: '',
      patientGender: '',
      cultureSource: '',
      pathogensFound: [],
      antimicrobialsUsed: [],
      cultureResults: [],
      cultureResultsDate: null,
      cultureTakenDate: null,
    },
    cultureErrors: ['invalid connection'],
  },
  getters: {
    culture: (state) => state.culture,
    cultureErrors: (state) => state.cultureErrors,
  },
  mutations: {
    ['UPDATE_CULTURE']: (state, culture) => state.culture = culture,
    ['ADD_UPLOADED_CULTURE']: (state, culture) => state.uploadedCultures.push(culture),
    ['ADD_CULTURE_ERROR']: (state, err) => state.cultureErrors.push(err),
  },
  actions: {
    async uploadCulture({ commit, getters }, culture) {
      commit('APP_LOADING', true, {rootState: true})

      try {
        const url = `${process.env.VUE_APP_BASE_URL}/api/cultures`
        const headers = addAuthorizationHeader(setJsonContentType(new Headers()))
        const body = JSON.stringify({
          culture: culture,
          uploaderId: getters.accountId,
        })
        console.log(body)
        const response = await fetch(url, {
            method: "POST",
            // cache: 'no-cache',
            body,
            headers,
          }
        );
        await validateResponse(response);
        commit('APP_LOADING', false, {rootState: true})
        commit('ADD_UPLOADED_CULTURE', culture)
      } catch (err) {
        commit('APP_LOADING', false, {rootState: true})
        commit('ADD_CULTURE_ERROR', err.message)
      }
    }
  }
};
