<template>
    <v-card>
        <v-toolbar v-if="$vuetify.breakpoint.xsOnly" dark color="primary">
            <v-btn icon dark @click="$emit('close')">
                <v-icon>close</v-icon>
            </v-btn>
            <v-toolbar-title>Filter Antibiogram</v-toolbar-title>
            <v-spacer></v-spacer>
        </v-toolbar>
        <v-card-title v-if="!$vuetify.breakpoint.xsOnly">
            <span class="headline primary--text">Add Custom Options to View Antibiogram</span>
        </v-card-title>
        <v-card-text>
            <v-layout column>
                <v-flex xs12 sm6 d-flex>
                    <v-select
                    placeholder="Select Scope of antibiogram"
                    v-model="antibiogramFilter.scope"
                    :items="scope"
                    label="Region Scope"
                    @input="changeScope"
                    @focus="errors.scopeErrors = null"
                    autofocus
                    prepend-icon="map"
                    required
                    :error-messages="errors.scopeErrors"
                    ></v-select>
                </v-flex>
                <v-flex xs12 sm6 d-flex>
                    <v-select
                    v-model="antibiogramFilter.duration"
                    :items="duration"
                    label="Duration"
                    placeholder="Past duration"
                    prepend-icon="access_time"
                    :error-messages="errors.durationErrors"
                    required
                    @change="errors.durationErrors = null"
                    ></v-select>
                </v-flex>
                <v-flex xs12 v-if="countyScope">
                    <v-combobox
                        v-model="antibiogramFilter.counties"
                        :hide-no-data="!countySearch"
                        :items="counties"
                        item-text="county"
                        :search-input.sync="countySearch" 
                        hide-selected
                        label="County/Counties"
                        placeholder="Search Counties"
                        hint="(Select up to 10 counties to merge antibiogram)"
                        :no-data-text="'No result for county '+countySearch"
                        multiple
                        small-chips
                        ref="countyRef"
                        @input="$refs.countyRef.blur()"
                        @change="errors.countyErrors = null"
                        return-object
                        validate-on-blur
                        @click:clear="$refs.countyRef.blur()"
                        prepend-icon="place"
                        :error-messages="errors.countyErrors"
                    >
                        <template v-slot:selection="{ item, parent, selected }">
                            <v-chip
                                color="red lighten-3"
                                :selected="selected"
                                label
                                small
                            >
                                <span class="pr-2">
                                {{ item.county }}
                                </span>
                                <v-icon
                                small
                                @click="parent.selectItem(item)"
                                >close</v-icon>
                            </v-chip>
                        </template>
                        <template v-slot:item="{ index, item }">
                          <v-list-tile-content>                           
                              <v-chip
                              label
                              small
                              >
                              {{ item.county }} County
                              </v-chip>
                          </v-list-tile-content>
                        </template>
                    </v-combobox>
                </v-flex>
                <v-flex xs12 v-if="subCountyScope">
                    <v-combobox
                        v-model="antibiogramFilter.subCounties"
                        :hide-no-data="!regionSearch"
                        :items="subCounties"
                        item-text="subCounty"
                        :search-input.sync="regionSearch" 
                        hide-selected
                        label="Sub-County/Sub-Counties"
                        placeholder="Search Sub-Counties"
                        hint="(Select up to 20 subCounties to merge antibiogram)"
                        :no-data-text="'No result for sub-county '+regionSearch"
                        multiple
                        small-chips
                        ref="regionRef"
                        @input="$refs.regionRef.blur();"
                        @change="errors.regionErrors = null"
                        return-object
                        validate-on-blur
                        @click:clear="$refs.regionRef.blur()"
                        prepend-icon="place"
                        :error-messages="errors.regionErrors"
                    >
                        <template v-slot:selection="{ item, parent, selected }">
                            <v-chip
                                color="green lighten-3"
                                :selected="selected"
                                label
                                small
                            >
                                <span class="pr-2">
                                {{ item.subCounty }}
                                </span>
                                <v-icon
                                small
                                @click="parent.selectItem(item)"
                                >close</v-icon>
                            </v-chip>
                        </template>
                        <template v-slot:item="{ index, item }">
                          <v-list-tile-content>                                
                              <v-chip
                              label
                              small
                              >
                              {{ item.subCounty }}
                              </v-chip>
                          </v-list-tile-content>
                        </template>
                    </v-combobox>
                </v-flex>
                <v-flex xs12 v-if="facilityScope">
                    <v-combobox
                        v-model="antibiogramFilter.facilities"
                        :hide-no-data="!facilitySearch"
                        :items="facilities"
                        item-text="facilityName"
                        :search-input.sync="facilitySearch" 
                        hide-selected
                        label="Facility/Hospital"
                        placeholder="Search Facilities/Hospitals"
                        hint="(Select up to 10 hospitals to merge antibiogram)"
                        :no-data-text="'No result for facility '+facilitySearch"
                        multiple
                        small-chips
                        ref="facilityRef"
                        @input="$refs.facilityRef.blur()"
                        @change="errors.facilityErrors = null"
                        return-object
                        validate-on-blur
                        @click:clear="$refs.facilityRef.blur()"
                        prepend-icon="local_hospital"
                        :error-messages="errors.facilityErrors"
                    >
                        <template v-slot:selection="{ item, parent, selected }">
                            <v-chip
                                color="cyan lighten-3"
                                :selected="selected"
                                label
                                small
                            >
                                <span class="pr-2">
                                {{ item.facilityName }}
                                </span>
                                <v-icon
                                small
                                @click="parent.selectItem(item)"
                                >close</v-icon>
                            </v-chip>
                        </template>
                        <template v-slot:item="{ index, item }">
                          <v-list-tile-content>                                
                              <v-chip
                              label
                              small
                              >
                              {{ item.facilityName }}
                              </v-chip>
                          </v-list-tile-content>
                        </template>
                    </v-combobox>
                </v-flex>
                <v-layout v-if="antibiogramFilter.advanced" column>
                    <v-flex>
                        <v-select
                        v-model="antibiogramFilter.advance.gender"
                        :items="genders"
                        label="Patients Gender"
                        placeholder="Select Gender"
                        prepend-icon="nature_people"
                        required
                        ></v-select>
                    </v-flex>
                    <v-layout row nowrap align-center style="padding: 0px;">
                        <v-flex d-inline-flex style="margin-right: 9px;"><v-icon>timeline</v-icon></v-flex>                    
                        <v-flex secondary--text text--lighten-2 subheading xs12>Patient Age Range (Years/Weeks)</v-flex>
                    </v-layout>           
                    <v-layout pa-0 justify-space-between align-baseline>
                        <v-flex shrink>
                            <v-switch
                            v-model="antibiogramFilter.advance.useWeeksForMinAge"
                            row
                            height="12"
                            label="Min Age"
                            color="primary"
                            hide-details></v-switch>      
                        </v-flex> 
                        <v-layout align-center>
                            <v-flex font-weight-regular grey--text text-xs-center>Toggle to use weeks for for range</v-flex>
                        </v-layout>
                        <v-flex shrink>
                            <v-switch
                            v-model="antibiogramFilter.advance.useWeeksForMaxAge"
                            label="Max Age"
                            color="primary"
                            hide-details></v-switch>
                        </v-flex>
                    </v-layout>
                    <v-layout>
                        <v-flex
                            shrink
                            :style="minSliderInputStyle"
                            >
                            <v-text-field
                                v-model="antibiogramFilter.advance.age[0]"
                                suffix="Years"
                                class="mt-0"
                                hide-details
                                single-line
                                type="number"
                                v-if="!this.antibiogramFilter.advance.useWeeksForMinAge"
                            ></v-text-field>
                            <v-text-field
                                v-model="antibiogramFilter.advance.weeks[0]"
                                suffix="Weeks"
                                class="mt-0"
                                hide-details
                                single-line
                                type="number"
                                v-else
                            ></v-text-field>
                        </v-flex>
                        <v-flex>
                            <v-range-slider
                                v-model="antibiogramFilter.advance.age"
                                :disabled="weekChosen"
                                max="100"
                                min="0"
                            ></v-range-slider>
                        </v-flex>
                        <v-flex
                            shrink
                            :style="maxSliderInputStyle"
                            >
                            <v-text-field
                                v-model="antibiogramFilter.advance.age[1]"
                                suffix="Years"
                                class="mt-0"
                                hide-details
                                single-line
                                type="number"
                                v-if="!this.antibiogramFilter.advance.useWeeksForMaxAge"
                            ></v-text-field>
                            <v-text-field
                                v-model="antibiogramFilter.advance.weeks[1]"
                                suffix="Weeks"
                                class="mt-0"
                                hide-details
                                single-line
                                type="number"
                                v-else
                            ></v-text-field>
                        </v-flex>                    
                    </v-layout>
                </v-layout>
                
            </v-layout>
        </v-card-text>
        <v-card-actions v-if="!$vuetify.breakpoint.xsOnly">
            <v-flex pl-2 utoh pcoh primary--text @click="antibiogramFilter.advanced = true" v-if="!antibiogramFilter.advanced">Show Advanced Filter Options?</v-flex>
            <v-flex pl-2 utoh pcoh primary--text @click="antibiogramFilter.advanced = false" v-else>Hide Advanced Filter Options?</v-flex>
            <v-spacer></v-spacer>
            <v-btn color="primary darken-1" flat @click="$emit('close')">Cancel</v-btn>
            <v-btn color="primary" @click="view">View Antibiogram</v-btn>
        </v-card-actions>
        <v-card-actions v-else>
            <v-layout column>
                <v-layout mb-2>
                    <v-flex pl-2 utoh pcoh primary--text @click="antibiogramFilter.advanced = true" v-if="!antibiogramFilter.advanced">Show Advanced Filter Options?</v-flex>
                    <v-flex pl-2 utoh pcoh primary--text @click="antibiogramFilter.advanced = false" v-else>Hide Advanced Filter Options?</v-flex>
                </v-layout>
                <v-layout>
                    <v-btn color="primary" @click="view" block>View Antibiogram</v-btn>
                </v-layout>
            </v-layout>
        </v-card-actions>
    </v-card>
</template>

<script>

import Counties from "@/xdata/counties"
import SubCounties from "@/xdata/districts"
import Facilities from "@/xdata/facilities"
import { mapMutations } from 'vuex';

export default {
    watch: {
      countiesModel (val) {
        if (val.length > 10) {
            this.$nextTick(() => this.countiesModel.pop())
        }
        this.countySearch = null
      },
    },
    props: {
        routeUrl: {
            type: String,
            default: "/antibiogram"
        }
    },
    computed: {
        countyScope() {
            return this.scopes.countyScope
        },
        subCountyScope() {
            return this.scopes.subCountyScope
        },
        facilityScope() {
            return this.scopes.facilityScope
        },
        weekChosen() {
            return this.antibiogramFilter.advance.useWeeksForMinAge || this.antibiogramFilter.advance.useWeeksForMaxAge
        },
        minSliderInputStyle() {
            if (this.antibiogramFilter.advance.useWeeksForMinAge) {
                return {
                    width: '125px',
                    marginRight: '10px'
                }
            }
            return {
                width: '115px',
                marginRight: '10px'
            }
        },
        maxSliderInputStyle() {
            if (this.antibiogramFilter.advance.useWeeksForMaxAge) {
                return {
                    width: '125px',
                    marginLeft: '10px'
                }
            }
            return {
                width: '120px',
                marginLeft: '10px'
            }
        }
    },
    created(){
        this.counties = Counties;
        this.subCounties = SubCounties;
        this.facilities = Facilities;
    },
    data() {
      return {
            antibiogramFilter: {
                scope: '',
                duration: '',
                counties: null,
                subCounties: null,
                facilities: null,
                values: [],
                advanced: false,
                advance: {
                    useWeeksForMinAge: false,
                    useWeeksForMaxAge: false,
                    gender: '',
                    age: [0, 99],
                    weeks: [1, 52],
                },
            },
            errors: {
                scopeErrors: null,
                durationErrors: null,
                countyErrors: null,
                regionErrors: null,
                facilityErrors: null,
            },
            scope: [
                "Country Level",
                "County Level",
                "Sub-County Level",
                "Facility/Hospital",
            ],
            scopes: {
                countyScope: false,
                subCountyScope: false,
                facilityScope: false
            },
            duration: [
                "Past 6 Months",
                "Past 1 Year",
                "Past 2 Years",
                "Past 4 Years",
                "Past 8 Years",
                "Past 16 Years",
                "Past 32 Years",
            ],
            min: 0,
            max: 100,
            countySearch: '',
            counties: null,
            regionSearch: '',
            subCounties: null,
            facilitySearch: '',
            facilities: null,
            genders: [
                "All Patients",
                "Male Patients Only",
                "Female Patients Only"
            ]
      }
    },
    methods: {
        ...mapMutations(['updateAntibiogramFilter']),
        changeScope() {
            this.scopes.countyScope = false;
            this.scopes.subCountyScope = false;
            this.scopes.facilityScope = false;

            this.errors.scopeErrors = null;

            switch (this.antibiogramFilter.scope) {
                case "County Level":
                    this.scopes.countyScope = true;                    
                    break;
                case "Sub-County Level":
                    this.scopes.subCountyScope = true;                    
                    break;
                case "Facility/Hospital":
                    this.scopes.facilityScope = true;                    
                    break;
            }
        },
        view() {
            let valid = true
            // Check scope
            if (this.antibiogramFilter.scope == "") {
                this.errors.scopeErrors = ["Scope is required"]
                valid = false
            }
            // Check duration
            if (this.antibiogramFilter.duration == "") {
                this.errors.durationErrors = ["Duration is required"]
                valid = false
            }

            if (!valid) {
                return
            }
            
            const values = []

            // Switch scope region
            switch (this.antibiogramFilter.scope) {
                case "County Level":
                    if (this.antibiogramFilter.counties == null) {
                        this.errors.countyErrors = ["At least one county is required"]
                        valid = false
                    } else{
                        for (const county of this.antibiogramFilter.counties) {
                            values.push({name: county.county, id: county.code})
                        }  
                    }                                                    
                    break;
                case "Sub-County/Regional":
                    if (this.antibiogramFilter.subCounties == null) {
                        this.errors.regionErrors = ["At least one region is required"]
                        valid = false
                    }else{
                        for (const subCounty of this.antibiogramFilter.subCounties) {
                            values.push({name: subCounty.subCounty, id: region.code})
                        }         
                    }                         
                    break;
                case "Facility/Hospital":
                    if (this.antibiogramFilter.facilities == null) {
                        this.errors.facilityErrors = ["At least one hospital is required"]
                        valid = false
                    }else{
                        for (const facility of this.antibiogramFilter.facilities) {
                            values.push({name: facility.facilityName, id: facility.facilityId})
                        }  
                    }                                      
                    break;
            }

            if (!valid) {
                return
            }

            if (this.antibiogramFilter.advance.gender !== "" || 
            (this.antibiogramFilter.advance.age[0] !== 0 &&  this.antibiogramFilter.advance.age[1] !== 99) || 
            (this.antibiogramFilter.advance.weeks[0] !== 1 && this.antibiogramFilter.advance.weeks[1] !== 52)) {
                this.antibiogramFilter.advanced = true
            }else{
                this.antibiogramFilter.advanced = false                
            }

            this.updateAntibiogramFilter({
                scope: this.antibiogramFilter.scope,
                duration: this.antibiogramFilter.duration,
                values: values,
                advanced: this.antibiogramFilter.advanced,
                advance: {
                    useWeeksForMinAge: this.antibiogramFilter.advance.useWeeksForMinAge,
                    useWeeksForMaxAge: this.antibiogramFilter.advance.useWeeksForMaxAge,
                    weekMin: this.antibiogramFilter.advance.weeks[0],
                    weekMax: this.antibiogramFilter.advance.weeks[1],
                    ageMin: this.antibiogramFilter.advance.age[0],
                    ageMax: this.antibiogramFilter.advance.age[1],
                    gender: this.antibiogramFilter.advance.gender
                }
            })

            this.$emit('close')

            this.$router.push(this.routeUrl)
            // this.antibiogramFilter.subCounties = null
            // this.antibiogramFilter.facilities = null
            // this.antibiogramFilter.counties = null
        }
    }
}
</script>

