<template>
    <v-card>
        <v-toolbar v-if="$vuetify.breakpoint.xsOnly" dark color="primary">
            <v-btn icon dark @click="CLOSE_REGISTER_DIALOG">
                <v-icon>close</v-icon>
            </v-btn>
            <v-toolbar-title class="title white--text">AntiBug
                <span><v-icon right>healing</v-icon></span>
            </v-toolbar-title>
            <v-spacer></v-spacer>
        </v-toolbar>
      <!-- <v-toolbar-side-icon @click.stop="drawer = !drawer" class="hidden-sm-and-up"></v-toolbar-side-icon> -->
        <v-card-title>
            <v-layout column justify-center align-center>
                <v-flex primary--text subheading font-weight-bold mb-1  v-if="!$vuetify.breakpoint.xsOnly">Antibug
                    <span><v-icon right color="primary" small>healing</v-icon></span>
                </v-flex>
                <v-divider></v-divider>
                <v-flex title font-weight-light mt-1>Register with Antibug</v-flex>
            </v-layout>            
        </v-card-title>
        <v-card-text>
            <v-flex v-if="registerErrors.length > 0">
                <v-layout red--text border-error pa-1 mb-2>
                    <v-layout column v-for="(registerError, i) in registerErrors" :key="i">
                        <v-flex text-xs-center xs12>
                            {{registerError}}
                        </v-flex>
                    </v-layout>             
                </v-layout>                
            </v-flex>
            <v-layout column>
                <v-form ref="form" v-model="valid" lazy-validation>
                    <v-layout row wrap>
                        <v-flex>
                            <v-text-field
                            label="Email Address *"
                            required
                            v-model="user.email"
                            @change="existEmail"
                            :rules="rules.emailRules"
                            validate-on-blur
                            :error-messages="emailErrorMessages"
                            prepend-icon="email"></v-text-field>
                        </v-flex>
                        <v-flex>
                            <v-text-field
                            label="Phone Number *"
                            required
                            v-model="user.phone"
                            @change="existPhone"
                            :rules="rules.phoneRules"
                            :error-messages="phoneErrorMessages"
                            validate-on-blur
                            prepend-icon="phone"></v-text-field>
                        </v-flex>
                    </v-layout>
                    <v-layout row wrap>
                        <v-flex>
                            <v-text-field
                            label="First Name *"
                            required
                            v-model="user.firstName"
                            :rules="rules.nameRules"
                            validate-on-blur
                            prepend-icon="perm_identity"></v-text-field>
                        </v-flex>
                        <v-flex>
                            <v-text-field
                            label="Last Name *"
                            required
                            v-model="user.lastName"
                            :rules="rules.nameRules"
                            validate-on-blur
                            prepend-icon="perm_identity"></v-text-field>
                        </v-flex>
                    </v-layout>
                    <v-layout row wrap>
                        <v-flex>
                            <v-text-field
                            label="Password *"
                            required
                            v-model="user.password"
                            :rules="rules.passwordRules"
                            validate-on-blur
                            :type="show1 ? 'text' : 'password'"
                            :prepend-icon="show1 ? 'visibility' : 'visibility_off'"
                            hint="At least 8 characters"
                            @click:prepend="show1 = !show1"></v-text-field>
                        </v-flex>
                        <v-flex>
                            <v-text-field
                            label="Confirm Password *"
                            required
                            v-model="user.confirmPassword"
                            :rules="[...rules.passwordRules, rules.passwordMatch]"
                            validate-on-blur
                            :type="show2 ? 'text' : 'password'"
                            :prepend-icon="show2 ? 'visibility' : 'visibility_off'"
                            hint="At least 8 characters"
                            @click:prepend="show2 = !show2"></v-text-field>
                        </v-flex>
                    </v-layout>
                    <v-flex pb-1>
                        <v-btn @click="fill" block flat>Fill</v-btn>
                        <v-btn @click="registerUser" block color="primary">Register</v-btn>
                    </v-flex>
                </v-form>
                <v-divider></v-divider>
                <v-layout column pt-1 justify-center align-center>
                    <v-flex>Or</v-flex>
                    <v-btn 
                    @click="registerGoogle" 
                    block
                    flat
                    >Register With Google
                        <v-avatar size="30">
                        <v-img
                        src="https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQhAR89CKjGGbr2BbyTNwFo4adLHY5DTG6jtMgK1FWsVv2oMSpe"
                        height="24"
                        width="24"></v-img>
                        </v-avatar>
                    </v-btn>
                </v-layout>
            </v-layout>
        </v-card-text>
        <v-card-actions>
            <v-flex>Have an Account? <span class="primary--text utoh pcoh" @click="login">Login</span></v-flex>
            <v-spacer></v-spacer>
            <v-btn
            flat
            color="primary"
            @click="CLOSE_REGISTER_DIALOG"
            >Cancel</v-btn>  
        </v-card-actions>
    </v-card>
</template>

<script>
import { mapMutations, mapActions, mapGetters } from 'vuex';
import { addAuthorizationHeader, setJsonContentType, validateResponse } from '@/mixins/fetch';

export default {
    computed: {
        ...mapGetters(['registerErrors']),
    },
    data() {
        return {
            show1: false,
            show2: false,
            valid: false,
            user: {
                email: '',
                phone: '',
                firstName: '',
                lastName: '',
                password: '',
                confirmPassword: '',
            },
            emailErrorMessages: [],
            phoneErrorMessages: [],
            rules: {
                emailRules: [
                    v => !!v || 'Email Address is required',
                ],
                passwordRules: [
                    v => !!v || 'Password is required',
                    v => v.length >= 8 || 'Password should be atleast 8 characters'
                ],
                phoneRules: [
                    v => !!v || 'Phone is required',
                ],
                nameRules: [
                    v => !!v || 'Name is required'
                ],
                passwordMatch: (v) => v === this.user.password || 'Password do not match',
            },
        }
    },
    methods: {
        ...mapActions(['register']),
        ...mapMutations(['CLOSE_REGISTER_DIALOG', 'SHOW_LOGIN_DIALOG']),
        registerUser() {
            if (this.$refs.form.validate() && this.emailErrorMessages.length < 1 
            && this.phoneErrorMessages.length < 1) {
                this.register(this.user)
            }
        },
        registerGoogle() {
            
        },
        login() {
            this.CLOSE_REGISTER_DIALOG()
            this.SHOW_LOGIN_DIALOG()
        },
        fill() {
            this.user.email = 'gideonhacer@gmail.com'
            this.user.phone = '716484395'
            this.user.firstName = 'Gideon'
            this.user.lastName = 'Kamau'
            this.user.password = 'haktivah11'
            this.user.confirmPassword = 'haktivah11'
        },
        async existEmail() {
            try {
                const url = `${process.env.VUE_APP_BASE_URL}/api/account/users/exists`
                const headers = setJsonContentType(addAuthorizationHeader(new Headers()))
                const body = JSON.stringify({
                    email: this.user.email
                })
                const response = await fetch(url, {
                    method: "POST",
                    cache: 'no-cache',
                    body,
                    headers,
                })
                await validateResponse(response)
                const res = await response.json()
                if (res.exists) {
                    this.emailErrorMessages.push('Email address is already registered')
                }else{
                    this.emailErrorMessages = []
                }
            } catch (err) {
                console.log("an error occured: "+err);
            }
        },
        async existPhone() {
            try {
                const url = `${process.env.VUE_APP_BASE_URL}/api/account/users/exists`
                const headers = setJsonContentType(addAuthorizationHeader(new Headers()))
                const body = JSON.stringify({
                    phone: this.user.phone
                })
                const response = await fetch(url, {
                    method: "POST",
                    cache: 'no-cache',
                    body,
                    headers,
                });
                await validateResponse(response)
                const res = await response.json()
                if (res.exists) {
                    this.phoneErrorMessages.push('Phone number is already registered')
                }else{
                    this.phoneErrorMessages = []
                }
            } catch (err) {
                console.log("an error occured: "+err);
            }
        }
    },
}
</script>

