  computed: {
    ...mapGetters(["isLoggedIn", "notificationsLen"]),
    loginDialog: {
      get: function() {
        return this.$store.getters.loginDialog;
      },
      set: function() {
        this.$store.commit("CLOSE_LOGIN_DIALOG");
      }
    },
    registerDialog: {
      get: function() {
        return this.$store.getters.registerDialog;
      },
      set: function() {
        this.$store.commit("CLOSE_REGISTER_DIALOG");
      }
    },
    registerSuccess: {
      get: function() {
        return this.$store.getters.successDialog;
      },
      set: function() {
        this.$store.commit("CLOSE_SUCESS_DIALOG");
      }
    }
  },
  data() {
    return {
      drawer: false
    };
  },
  methods: {
    ...mapMutations(["CLOSE_LOGIN_DIALOG", "SHOW_LOGIN_DIALOG"]),
    ...mapActions(["logout"]),
    goto(url) {
      this.$router.push(url);
    }
  }
};
