import {
	validateResponse, addDefaultAuthorizationHeader, setJsonContentType
} from "@/mixins/fetch";

export const loginModule = {
	state: {
		login: {
			errors: [],
			state: false,
			dialog: false,
			defaultToken: null,
			token: null,
			accountId: null,
		},
	},
	mutations: {
		['SET_LOGIN_ERRORS'] (state, errors) {
			state.login.errors = errors
		},
		['SET_LOGIN_CREDENTIALS'] (state, payload) {
			state.login.errors = []
			state.login.state = true
			state.login.dialog = false
			state.login.token = payload.token
			state.login.accountId = payload.account_id
			localStorage.setItem('token', payload.token)
			localStorage.setItem('id', payload.account_id)
		},
		['SET_DEFAULT_CREDENTIALS'] (state, token) {
			state.login.defaultToken = token
			localStorage.setItem('default', token)
		},
		['SHOW_LOGIN_DIALOG'] (state) {
			state.login.dialog = true
		},
		['CLOSE_LOGIN_DIALOG'] (state) {
			state.login.dialog = false
		},
		['SIGN_OUT'] (state) {
			state.login = {
				errors: [],
				state: false,
				dialog: false,
				defaultToken: '',
				token: '',
				accountId: '',
			}
			// clear localStorage
			localStorage.removeItem('id')
			localStorage.removeItem('token')
		},
	},
	getters: {
		loginDialog: (state) => state.login.dialog,
		loginState: (state) => state.login.state,
		loginErrors: (state) => state.login.errors,
		fullName: (state) => 'Max',
		accountId: (state) => state.login.accountId || localStorage.getItem('id'),
		accountToken: (state) => state.login.token || localStorage.getItem('token'),
		defaultToken: (state) => state.login.defaultToken || localStorage.getItem('default'),
		isLoggedIn: (state, getters) => getters.accountId != null  && getters.accountToken != null,
	},
	actions: {
		async getDefaultCredentials({ commit }) {
			// Get token
			try {
				const url = `${process.env.VUE_APP_BASE_URL}/api/account/token`
				const response = await fetch(url);
				await validateResponse(response);
				const token = await response.json()
				commit('SET_DEFAULT_CREDENTIALS', token)
			  } catch (err) {
				console.log("an error occured: "+err);
			  }
		},
		async logout({ commit }) {
			commit('APP_LOADING', true, {rootState: true})

			setTimeout(() => {
				commit('APP_LOADING', false, {rootState: true})
				commit('SIGN_OUT')
			}, 500)
		},
		async loginWithEmailOrPhone({ commit }, loginPayload) {
			commit('APP_LOADING', true, {rootState: true})

			try {
                const url = `${process.env.VUE_APP_BASE_URL}/api/account/users/login`
                const headers = setJsonContentType(addDefaultAuthorizationHeader(new Headers()))
                const loginRequest = {
					creds: {
						email: loginPayload.email,
						phone: loginPayload.email,
						password: loginPayload.password,
					},
				}
				const body = JSON.stringify(loginRequest)
                const response = await fetch(url, {
                    method: 'POST',
                    cache: 'no-cache',
                    body,
                    headers,
                });
				let res = await validateResponse(response)
				let resJson = await res.json()
				commit('SET_LOGIN_CREDENTIALS', resJson)
				commit('CLOSE_LOGIN_DIALOG')				
				commit('APP_LOADING', false, {rootState: true})
            } catch (err) {
				commit('SET_LOGIN_ERRORS', [err.message])
				commit('APP_LOADING', false, {rootState: true})
            }
		},
		async loginGoogle({ commit }, loginPayload) {
			// Do stuff
			console.log(loginPayload)
		}
	},
};
