import Vue from "vue";
import Vuex from "vuex";
import { loginModule } from "@/store/modules/login";
import { registerModule } from "@/store/modules/register";
import { account } from "@/store/modules/account";
import { profile } from "@/store/modules/profile";
import { notification } from "@/store/modules/notification";
import { antibiogram } from "@/store/modules/antibiogram";
import { culture } from "@/store/modules/culture";
import { antimicrobial } from "@/store/modules/antimicrobial";
import { pathogen } from "@/store/modules/pathogen";

Vue.use(Vuex);

export default new Vuex.Store({
	modules: {
		registerModule,
		loginModule,
		account,
		profile,
		notification,
		antibiogram,
		culture,
		antimicrobial,
		pathogen
	},
	state: {
		loading: false,
	},
	getters: {
		loading: (state) => state.loading, 
	},
	mutations: {
		['APP_LOADING']: (state, loading) => {
			state.loading = loading
		}
	}
});
