<template>
  <v-app>    
    <header-comp></header-comp>
    <v-container fluid pa-0 app mb-3>
      <v-container fluid pa-2>
      <v-layout justify-center>
        <v-flex xs12 sm6>
          <v-layout fluid>
            <v-breadcrumbs :items="breadCrumbsItems"  divider=">">
              <template  v-slot:item="props">
                <router-link tag="span" :to="props.item.href" :class="[props.item.disabled ? 'disabled' : 'primary--text']">{{props.item.text}}</router-link>
              </template>
            </v-breadcrumbs>
          </v-layout>        
        </v-flex>
      </v-layout>
      </v-container>
      <router-view></router-view>
    </v-container>
    <footer-comp></footer-comp>
  </v-app>
</template>

<script>
import Header from '@/components/core/header';
import Footer from '@/components/core/footer';
import {mapGetters, mapActions} from 'vuex';

export default {
	name: 'App',
	components: {'header-comp':Header, 'footer-comp':Footer},
	created() {
		if (!this.isLoggedIn) {
			// get default token
			this.getDefaultCredentials()
		}
	},
	beforeUpdate() {
		const slashes = this.countSubString(this.$route.path)

		if (slashes > this.routerPath.previous) {
		this.breadCrumbs.push({
			text: this.$route.name,
			disabled: true,
			href: this.$route.fullPath,
		})
		for (let i = 0; i < this.breadCrumbs.length-1; i++) {
			this.breadCrumbs[i].disabled = false        
		}
		} else if (slashes < this.routerPath.previous) {
		this.breadCrumbs.pop()
		this.breadCrumbs[this.breadCrumbs.length-1].disabled = true
		} else if (slashes === this.routerPath.previous && this.breadCrumbs.length === 2) {
		this.breadCrumbs.pop()
		this.breadCrumbs[0].disabled = true
		} else  if (slashes === this.routerPath.previous && this.breadCrumbs.length === 1) {
		this.breadCrumbs.push({
			text: this.$route.name,
			disabled: true,
			href: this.$route.fullPath,
		})
		this.breadCrumbs[0].disabled = false
		}

		this.routerPath.previous = slashes
	},
	computed: {
		...mapGetters(['isLoggedIn']),
		breadCrumbsItems() {
			return this.breadCrumbs
		},
	},
	data () {
		return {
		dialog: false,
		drawer: false,
		routerPath: {
			prevPath: "", 
			previous: 1,
		},
		breadCrumbs: [
			{
			text: 'Home',
			disabled: false,
			href: '/'
			},
		],
		};
	},
	methods: {
		...mapActions(['getDefaultCredentials']),
		goto: url => history.go(url),
		updateBreadCrumb: (path) => {
			console.log(path)
			this.routerPath.prevPath = path
		},
		countSubString(string) {
			string += "";
			const subString = "/";
			let allowOverlapping = true;
			if (subString.length <= 0) return (string.length + 1);

			var n = 0,
				pos = 0,
				step = allowOverlapping ? 1 : subString.length;

			while (true) {
				pos = string.indexOf(subString, pos);
				if (pos >= 0) {
					++n;
					pos += step;
				} else break;
			}
			return n;
		}
	},
};
</script>

